:root {
  --color-background: #282c34;
  --color-link: #61dafb;
  --color-white: #fff;
  --color-dark-gray: #333;
  --color-blue: #007bff;
  --color-light-gray: #ccc;
  --color-darker-blue: #0056b3;
  --color-gray: #888;

  --font-size-small: 14px;
  --font-size-normal: 16px;
  --font-size-large: 24px;

  --color-light-border: #ddd;
  --color-black: #000;
  --color-lighter-border: #eee;
  --color-disabled-text: #bbb;
  --color-submit-button: #535151;
  --color-modal-overlay: rgba(0, 0, 0, 0.5);
  --color-medium-gray: #999;
  --color-darker-text: #444;

  --color-very-dark: #080808;
  --color-input-border: #141414;
  --color-info-box-bg: #f7f0f0;
  --color-info-box-border: #ebe3e3;
  --color-info-text: #191a17;
  --color-leave-button: #ff5a5a;
  --color-modal-shadow: rgba(0, 0, 0, 0.2);

  --font-size-smaller: 13px;
  --font-size-medium: 15px;

  --color-comment-text: #555;
  --color-delete-button: #dc3545;

  --font-size-comment: 0.9rem;
  --font-size-comment-date: 0.8rem;
  --font-size-comment-body: 0.95rem;

  --color-card-shadow: rgba(0, 0, 0, 0.05);
  --color-order-status-1: #e0f2ff;
  --color-order-status-text-1: #0077cc;
  --color-order-status-2: #fff4e5;
  --color-order-status-text-2: #ff9800;
  --color-order-status-3: #e6f4ea;
  --color-order-status-text-3: #2e7d32;
  --color-order-status-4: #fbeaea;
  --color-order-status-text-4: #d32f2f;

  --font-size-order-status: 12px;
  --color-menu-text: #666;
  --color-order-date: #a7a4a4;
  --font-size-title: 32px;

  --color-progress-bar: #4CAF50;
    --font-size-xsmall: 10px;

    --color-teal: #4fd1c5;
    --color-reward-select: #fdf6e3;

    --color-card-shadow-strong: rgba(0, 0, 0, 0.1);
    --font-size-heart: 18px;
  --color-footer-bg: #f8f8f8;
  --color-footer-border: #e0e0e0;
  --color-table-border: #e5e5e5;
  --color-table-hover: #fafafa;
    --color-comment-border: #f1f1f1;
  --font-size-customtab: 1.05rem;
  --font-size-tableheader: 0.85rem;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

